
@import "../node_modules/normalize.css";
@font-face {
  font-family: TimeBurner;
  src: url('../static/fonts/timeburnernormal.ttf');
}
@font-face {
  font-family: TimeBurnerBold;
  src: url('../static/fonts/timeburnerbold.ttf');
}
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: TimeBurner, sans-serif;
}
[v-cloak] {
  display: none;
}
img,
.row,
.container {
  max-width: 100%;
}
.pl-app {
  background: #424242;
  color: #fff;
}
.pl-app .toolbar {
  background: #202020;
  height: 64px;
}
.pl-app .toolbar .container,
.pl-app .toolbar .middle-xs {
  height: 100%;
}
.pl-app .toolbar .logo {
  text-decoration: none;
  color: inherit;
}
.pl-app .toolbar .logo h1 span:first-of-type {
  border: 1px solid;
  padding-left: 5px;
  padding-right: 5px;
}
.pl-app main {
  min-height: calc(100vh - 128px);
}
.pl-app footer a {
  color: inherit;
}



